// Add click event listener to the copy button
copyFunc.addEventListener('click', () => {
    // Use the Clipboard API to copy the content
    navigator.clipboard.writeText(cashlyinkElement.textContent) // Use innerHTML if copying HTML
        .then(() => {
            // Change the copy button to the success icon
            copyFunc.src = successGif;
            // After 2 seconds, revert back to the original icon
            setTimeout(() => {
                copyFunc.src = copyIco;
            }, 2000); // Change icon back after 2000ms (2 seconds)
        })
        .catch(err => {
            console.error('Failed to copy: ', err);
        });
});


