import logoSrc from '../../../images/icons/logo.png';
import '../../../css/splash.css'; 
const splashWrapper = document.createElement('div');
splashWrapper.classList.add('splashWrapper');
const logoImg = document.createElement('img');
logoImg.src = logoSrc;
logoImg.alt = 'Cashly Logo';
logoImg.classList.add('logoImg');
const splashText = document.createElement('h1');
splashText.textContent = 'Cashly';
splashText.style.color = 'white';
splashText.style.textAlign = 'center';
splashWrapper.appendChild(logoImg);
splashWrapper.appendChild(splashText);
document.body.appendChild(splashWrapper);

export default splashWrapper;
