//inactive
import homeImgSrc from '../assets/icons/home.svg';
import activityImgSrc from '../assets/icons/activity.svg';
import walletImgSrc from '../assets/icons/wallet.svg';
import navbarProfileImgSrc from '../assets/icons/support.svg';
//active
import homeSel from '../assets/icons/selected/homeSel.svg';
import activitySel from '../assets/icons/selected/activitySel.svg';
import walletSel from '../assets/icons/selected/walletSel.svg';
//
import { dashWrapper } from '../../..';
import { transactionHistoryWrapper } from '../../activity';
import { walletWrapper } from '..';
import { supportWrapper } from '../../support';
import '../assets/css/navbar.css';
export const navbarWrapper = document.createElement('div');
const navbarHomeHolder = document.createElement('div');
const navbarActivityHolder = document.createElement('div');
const navbarPaymentHolder = document.createElement('div');
const navbarProfileHolder = document.createElement('div');
const navbarProfileImg = document.createElement('img');

//elements
const homeImg = document.createElement('img');
const activityImg = document.createElement('img');
const walletImg = document.createElement('img');
//
navbarProfileImg.src = navbarProfileImgSrc;
navbarProfileImg.alt = 'Profile';
homeImg.src = homeImgSrc;
homeImg.alt = 'Home';
activityImg.src = activityImgSrc;
activityImg.alt = 'Activity';
walletImg.src = walletImgSrc;
walletImg.alt = 'Wallet';


//classlist
navbarWrapper.classList = 'navbarWrapper';
navbarHomeHolder.classList = 'navbarHomeHolder';
navbarActivityHolder.classList = 'navbarActivityHolder';
navbarPaymentHolder.classList = 'navbarPaymentHolder';
navbarProfileHolder.classList = 'navbarProfileHolder';
navbarProfileImg.classList = 'navbarProfileImg';
homeImg.classList = 'homeImg';
activityImg.classList = 'activityImg';
walletImg.classList = 'walletImg';
navbarProfileImg.classList = 'navbarProfileImg';
//
navbarProfileImg.src = navbarProfileImgSrc;
navbarProfileImg.alt = 'Profile';
navbarHomeHolder.appendChild(homeImg);
navbarActivityHolder.appendChild(activityImg);
navbarPaymentHolder.appendChild(walletImg);
navbarProfileHolder.appendChild(navbarProfileImg);
navbarWrapper.appendChild(navbarHomeHolder);
navbarWrapper.appendChild(navbarActivityHolder);
navbarWrapper.appendChild(navbarPaymentHolder);
navbarWrapper.appendChild(navbarProfileHolder);


navbarHomeHolder.addEventListener('click',()=>{
document.body.innerHTML = "";
homeImg.src = homeSel;
walletImg.src = walletImgSrc;
activityImg.src = activityImgSrc;
document.body.appendChild(navbarWrapper); // Re-append the navbar
document.body.appendChild(dashWrapper);
});
navbarActivityHolder.addEventListener('click',()=>{
    homeImg.src = homeImgSrc;
    walletImg.src = walletImgSrc;
activityImg.src = activityImgSrc;
    document.body.innerHTML = "";
    activityImg.src = activitySel;
    
    document.body.appendChild(navbarWrapper); // Re-append the navbar
    document.body.appendChild(transactionHistoryWrapper);
    });
    navbarPaymentHolder.addEventListener('click',()=>{
        homeImg.src = homeImgSrc;
        activityImg.src = activityImgSrc;
        document.body.innerHTML = "";
        walletImg.src = walletSel;
        document.body.appendChild(navbarWrapper); // Re-append the navbar
        document.body.appendChild(walletWrapper);
        });
        navbarProfileHolder.addEventListener('click',()=>{
            homeImg.src = homeImgSrc;
            walletImg.src = walletImgSrc;
            activityImg.src = activityImgSrc;
            document.body.innerHTML = "";
            document.body.appendChild(supportWrapper);
            });