//top level appends

// Function to create and append sample accounts to the linkedAccountsWrapper
function createSampleAccountsAndAppend() {
    // Sample account data
    const sampleAccounts = [
        { name: 'Savings Account', balance: 1500.50 },
        { name: 'Checking Account', balance: 320.75 },
        { name: 'Business Account', balance: 9800.22 },
        { name: 'Investment Account', balance: 12000.00 }
    ];

    // Clear existing accounts if any
    linkedAccountsWrapper.innerHTML = '';

    // Iterate over the sample accounts and append them
    sampleAccounts.forEach(account => {
        const accountElement = document.createElement('div');
        accountElement.classList.add('account-item');

        const accountName = document.createElement('p');
        accountName.textContent = account.name || 'Unnamed Account';
        accountName.classList.add('account-name');

        const accountBalance = document.createElement('p');
        accountBalance.textContent = `$${account.balance.toFixed(2)}` || '0.00';
        accountBalance.classList.add('account-balance');

        // Append account name and balance to account element
        accountElement.appendChild(accountName);
        accountElement.appendChild(accountBalance);

        // Insert the account element before the edit button
        linkedAccountsWrapper.insertBefore(accountElement, editBtn.nextSibling);
    });
}

// Call the function to create and append the sample accounts
createSampleAccountsAndAppend();

