import './index.css';
import { bottomsheetWrapper, bottomSheetContent, dashWrapper,bottomsheetBar,btcWrapper } from '../..';

export function bottomsheetDashboard(content) {
    const sheetHeight = bottomsheetWrapper.offsetHeight;
    const maxScale = 0.9; // Minimum scale value
    const restTranslateY = 6 * 37.8; // Convert 6cm to pixels (1cm = 37.8px)
    let startY;
    let currentY;
    let startTime;
    let dragging = false;
    
    bottomsheetWrapper.style.transform = 'translateY(0)';

    // Bottomsheet set content
    bottomSheetContent.appendChild(content);

    const startDrag = (e) => {
        startY = e.touches ? e.touches[0].clientY : e.clientY;
        startTime = new Date().getTime();
        dragging = true;
        bottomsheetWrapper.style.transition = 'none';  // Disable transition during drag
        document.body.style.cursor = 'grabbing';
    };

    const drag = (e) => {
        if (!dragging) return;
        currentY = e.touches ? e.touches[0].clientY : e.clientY;
        const deltaY = currentY - startY;

        if (deltaY >= 0) {
            const percentage = deltaY / sheetHeight;
            const scale = maxScale + (1 - maxScale) * percentage;
            bottomsheetWrapper.style.transform = `translateY(${deltaY}px)`;
        }
    };

    const endDrag = (e) => {
        if (!dragging) return;
        dragging = false;
        document.body.style.cursor = 'default';

        const deltaY = (e.changedTouches ? e.changedTouches[0].clientY : e.clientY) - startY;
        const endTime = new Date().getTime();
        const timeTaken = endTime - startTime;
        const speed = Math.abs(deltaY / timeTaken);

        // Restore transitions for smooth snapping
        bottomsheetWrapper.style.transition = `transform ${speed > 0.5 ? '0.2s' : '0.3s'} ease-in-out`;

        // Check if the bottom sheet should float at 6cm or snap back

        if (speed > 1) {
            // Snap to 6cm (resting position)
            bottomsheetWrapper.style.transform = `translateY(${restTranslateY}px)`;
            dashWrapper.insertBefore(btcWrapper, bottomsheetWrapper);

        } else {
            // Snap back to full open position
            bottomsheetWrapper.style.transform = 'translateY(0)';
            dashWrapper.removeChild(btcWrapper);

        }
    };

    // Mouse events
    bottomsheetWrapper.addEventListener('mousedown', startDrag);
    document.addEventListener('mousemove', drag);
    document.addEventListener('mouseup', endDrag);

    // Touch events for mobile
    bottomsheetWrapper.addEventListener('touchstart', startDrag);
    document.addEventListener('touchmove', drag);
    document.addEventListener('touchend', endDrag);

    bottomsheetWrapper.addEventListener('transitionend', () => {
        if (bottomsheetWrapper.style.transform === 'translateY(100%)') {
            bottomsheetWrapper.style.transform = 'translateY(100%)';
        }
    });
    
// Add event listener for bottomsheetBar to trigger resting position
bottomsheetBar.addEventListener('click',()=>{
    console.log('www');
    });
}
