import './assets/css/index.css';
import sendArrowSrc from './assets/icons/rightArrow.png'; // Add your send arrow SVG here

export const supportWrapper = document.createElement('div');
const supportHeader = document.createElement('div');
const supportHeaderTitle = document.createElement('h4');
const chatHolder = document.createElement('div');
const messageInputHolder = document.createElement('div');
const messageInput = document.createElement('input');
const sendButtonHolder = document.createElement('div');
const sendButtonImg = document.createElement('img');

// Set class names
supportWrapper.classList = 'supportWrapper';
supportHeader.classList = 'supportHeader';
chatHolder.classList = 'chatHolder';
messageInputHolder.classList = 'messageInputHolder';
messageInput.classList = 'messageInput';
sendButtonHolder.classList = 'sendButtonHolder';
sendButtonImg.classList = 'sendButtonImg';

// Set text and attributes
supportHeaderTitle.textContent = 'Support';
messageInput.placeholder = 'Type your message...';
sendButtonImg.src = sendArrowSrc;
sendButtonImg.alt = 'Send';

// Build the structure
supportHeader.appendChild(supportHeaderTitle);
messageInputHolder.appendChild(messageInput);
sendButtonHolder.appendChild(sendButtonImg);
messageInputHolder.appendChild(sendButtonHolder);
supportWrapper.appendChild(supportHeader);
supportWrapper.appendChild(chatHolder);
supportWrapper.appendChild(messageInputHolder);

// Event listener for the send button
sendButtonHolder.addEventListener('click', () => {
  const message = messageInput.value;
  if (message) {
    const messageBubble = document.createElement('div');
    messageBubble.classList = 'messageBubble';
    messageBubble.textContent = message;
    chatHolder.appendChild(messageBubble);
    messageInput.value = ''; // Clear input after sending
  }
});
