import './assets/css/index.css';
import { navbarWrapper } from '../wallet/modules/navbar';
export const transactionHistoryWrapper = document.createElement('div');
const searchBarWrapper = document.createElement('div');
const transactionListWrapper = document.createElement('div');
const filterWrapper = document.createElement('div');

// Top search bar
const searchBar = document.createElement('input');
searchBar.type = 'text';
searchBar.placeholder = 'Search transactions...';

// Filter section
const filterTitle = document.createElement('h4');
filterTitle.textContent = 'Filters';
const filterSelect = document.createElement('select');
const filterOptionAll = document.createElement('option');
filterOptionAll.value = 'all';
filterOptionAll.textContent = 'All';
const filterOptionSuccess = document.createElement('option');
filterOptionSuccess.value = 'success';
filterOptionSuccess.textContent = 'Success';
const filterOptionFailed = document.createElement('option');
filterOptionFailed.value = 'failed';
filterOptionFailed.textContent = 'Failed';
filterSelect.appendChild(filterOptionAll);
filterSelect.appendChild(filterOptionSuccess);
filterSelect.appendChild(filterOptionFailed);

transactionHistoryWrapper.classList = 'transactionHistoryWrapper';
searchBarWrapper.classList = 'searchBarWrapper';
transactionListWrapper.classList = 'transactionListWrapper';
filterWrapper.classList = 'filterWrapper';

searchBar.classList = 'searchBar';

filterTitle.classList = 'filterTitle';
filterSelect.classList = 'filterSelect';
filterOptionAll.classList = 'filterOptionAll';
filterOptionSuccess.classList = 'filterOptionSuccess';
filterOptionFailed.classList = 'filterOptionFailed';


// Transaction list
const transactions = [
    { type: 'Mpesa', amount: '$1,000', status: 'Success' },
    { type: 'Bank Transfer', amount: '$500', status: 'Failed' },
    { type: 'Topup', amount: '$200', status: 'Success' },
    { type: 'Withdrawal', amount: '$300', status: 'Success' }
];

// Dynamically generate transaction items
transactions.forEach(transaction => {
    const transactionItem = document.createElement('div');
    transactionItem.classList = 'transactionItem';

    const transactionType = document.createElement('p');
    transactionType.textContent = transaction.type;

    const transactionAmount = document.createElement('p');
    transactionAmount.textContent = transaction.amount;

    const transactionStatus = document.createElement('p');
    transactionStatus.textContent = transaction.status;
    transactionStatus.classList = transaction.status === 'Success' ? 'statusSuccess' : 'statusFailed';

    transactionItem.appendChild(transactionType);
    transactionItem.appendChild(transactionAmount);
    transactionItem.appendChild(transactionStatus);
    transactionListWrapper.appendChild(transactionItem);
});
//

// Append all elements
searchBarWrapper.appendChild(searchBar);
filterWrapper.appendChild(filterTitle);
filterWrapper.appendChild(filterSelect);

transactionHistoryWrapper.appendChild(searchBarWrapper);
transactionHistoryWrapper.appendChild(filterWrapper);
transactionHistoryWrapper.appendChild(transactionListWrapper);
// transactionHistoryWrapper.appendChild(navbarWrapper);

// activityWrapper.appendChild(transactionHistoryWrapper);

// Example CSS class toggle based on filter
filterSelect.addEventListener('change', function() {
    const selectedFilter = filterSelect.value;
    const allTransactions = transactionListWrapper.querySelectorAll('.transactionItem');
    allTransactions.forEach(item => {
        const status = item.querySelector('p:last-child').textContent.toLowerCase();
        if (selectedFilter === 'all' || status === selectedFilter) {
            item.style.display = 'block';
        } else {
            item.style.display = 'none';
        }
    });
});
